@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  font-family: Lato, Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  text-align: center;
  background-color: #f4f4f4;
}

a {
  margin: 10px;
  text-decoration: none;
  color: #1fb18d;
}

a:visited {
  color: #1fb18d;
}

button {
  font-size: 1.1rem;
  padding: 5px;
  margin: 5px;
}

input {
  font-size: 1.2rem;
  padding: 5px;
  margin-left: 10px;
  margin-bottom: 5px;
}

.home-container {
  width: 70%;
  text-align: center;
  background-color: #f4f4f4;
  margin: auto;
}

.title {
  background-color: forestgreen;
  color: white;

  font-size: 50px;
  padding: 40px;

  /* border-radius: 0 0 300px 300px; */
  margin: auto;

  cursor: default;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.title p {
  font-size: 1rem;
}

.route-link {
  /* margin-top: 1rem; */
  background-color: #333333;
  padding: 30px;
}

.route-link a {
  text-decoration: none;
  margin-right: 15px;
  color: #f4f4f4;
  background-color: #333333;
  padding: 1rem;
  border: 1px solid #333333;
  border-radius: 7px;

  transition: 0.2s ease-in;
}

.route a:visited {
  color: white;
}

.route-link a:hover {
  opacity: 0.9;
  color: dodgerblue;
  border: 1px solid rgba(30, 143, 255, 0.466);
  background-color: white;
}

.balance {
  font-size: 3rem;
}

.data-container {
  width: 100%;
  margin: auto;
}

.display-data {
  font-size: 30px;
  background-color: white;

  width: 30%;
  height: 400px;
  margin: 1rem;
  margin-bottom: 2rem;

  border: 1px dotted dodgerblue;
  border-radius: 8px;

  float: left;
  box-sizing: border-box;

  overflow: hidden;
}

@media (max-width: 500px) {
  .display-data {
    width: 80%;
  }
}

@media (min-width: 501px) and (max-width: 950px) {
  .display-data {
    width: 40%;
  }
}

.id {
  font-size: 12px;
  color: rgb(160, 160, 160);
}

.input-surround {
  text-align: center;
}

.debit-credit-title {
  font-size: 3.5rem;
  color: #333333;
  font-weight: 700;
}

.debit-credit-nav {
  width: 64%;
  margin: auto;
  font-size: 1rem;
  padding: 3rem;
  background-color: #333333;
}

.debit-credit-nav a {
  color: #f4f4f4;
  margin-right: 1rem;
  text-decoration: none;
  padding: 1rem;
  border: 1px solid #333333;
  border-radius: 7px;

  transition: 0.2s ease-in;
}

.debit-credit-nav a:visited {
  color: white;
}

.debit-credit-nav a:hover {
  opacity: 0.9;
  color: dodgerblue;
  border: 1px solid rgba(30, 143, 255, 0.466);
  background-color: white;
}

.debit-credit-nav button {
  margin-right: 1rem;
  padding: 18px;
  font-size: 1rem;
  color: #f4f4f4;
  background-color: #333333;
  border: 1px solid #333333;
  border-radius: 7px;
  outline: none;

  transition: 0.2s ease-in;
}

.debit-credit-nav button:hover {
  cursor: pointer;
  opacity: 0.9;
  color: dodgerblue;
  border: 1px solid rgba(30, 143, 255, 0.466);
  background-color: white;
}

